/**                . */
function translateProductFieldName(name: string): string {
    if (name === "imgBig") return "URL     ";
    if (name === "imgSmall") return "URL     ";
    if (name === "name") return " ";
    if (name === "dimensionType") return "  ";
    if (name === "dimension") return " (.) /  (.)";
    if (name === "barcode") return "";
    if (name === "manufacturer") return "";
    if (name === "brand") return "";
    if (name === "description") return "";
    if (name === "price") return "";
    if (name === "purposes") return "";
    return "   ";
}

export default translateProductFieldName;
